.App {
  margin: auto;
  text-align: center;
  max-width: 1200px;
  display: flex;
  flex: 1;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
}

.App-wrap {
  /* flex: 1; */
  /* max-width: 50%; */
  /* word-wrap: break-word; */
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}

#view {
  height: 75%;
  overflow: scroll;
}

img {
  max-width: 100%;
}

.ck.ck-editor__main>.ck-editor__editable, .ck.ck-editor__main>.ck-editor__editable {
  height: 150px !important;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
